:root {
  // --brand-clr__red: rgba(226, 54, 54, 0.95);
  --brand_clr__1: #2196f3;
  --brand_clr__1-hover: #2e9cf7;
  --brand_clr__1-active: #1b8eec;
  --brand_clr__1-01: #cfe9ff;
  --brand_clr__2: #2f303a;
  --brand_clr__4: #2b2b2b;
  --brand_clr__2_light: rgb(221, 221, 221);
  --brand_clr__2-active: #4c4d57;
  --brand_clr__2-hover: #4c4d5798;
  --background_clr__main: #ffffff;
  --zone-special_clr: #f1f1f1;
  --zone-special_clr: rgba(219, 219, 219, 0.2);

  /* TEXT */
  --text-clr__primary: #1a1a1a;
  --text-clr__secondary: #6b6b6b;
  /* ICONS */
  --icon-clr__primary: #808080;
  /* TABLES */
  --tr-cell-bckgrnd_clr__even: #dadada7e;
  --tr-bckgrnd_clr__even: #dadada7e;
  --tr-brdr-clr__primary: #c4c4c481;
  --tr-bckrnd-clr__hover: rgba(103, 202, 255, 0.15);
  --tr-bckrnd-clr__selected: rgba(103, 202, 255, 0.3);
  --td-bckrnd-clr__active: rgba(137, 202, 255, 0.7);
  --td-outline-clr: rgba(137, 202, 255, 1);
  --th-font-size__primary: 12px;
  --th-font-size__secondary: 14px;
  --th-font-weight: 700;
  --td-font-size__primary: 12px;
  --td-font-size__secondary: 14px;

  --row_shadow: 0 0 2px rgb(173, 173, 173);
  --row_shadow__hover: 0 0 5px rgb(122, 122, 122);
  /* BUTTONS */
  --btn-deciscon_clr__primary: rgba(219, 219, 219, 0.4);
  --btn-deciscon_clr__hover: rgba(219, 219, 219, 0.5);
  --btn-deciscon_clr__active: rgb(242, 242, 242);

  --btn_bckgrnd-color: transparent;
  --btn_bckgrnd-color-hover: rgba(219, 219, 219, 0.1);
  --btn_bckgrnd-color-active: rgba(219, 219, 219, 0.05);
  /* OTHERS */
  --header-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
  --block-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  --block-hdr-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  --block-ftr-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  --button-shadow-hover: 0px 1px 9px rgba(0, 0, 0, 0.2);
  --button-shadow-active: 0px 1px 5px rgba(0, 0, 0, 0.1);

  --timing-function__main: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  --test-outline_d: 1px dashed tomato;
  --test-outline_s: 1px solid tomato;

  //? notifications =========

  --clr-light: #fff;
  --clr-dark: #121212;
  --clr-default: rgb(154, 154, 154);
  --clr-info: #3498db;
  --clr-success: #07bc0c;
  --clr-warning: #f1c40f;
  --clr-error: #e74c3c;
  --clr-primary: #cdcdcd;

  --desktop: 1200px;
}
